import React from "react";
import { Link } from "gatsby";
import { Drawer } from "antd";

export default function Nav(props) {
  const { onMenuToggle, visible } = props;

  return (
    <nav id="nav">
      <ul>
        <li className="special">
          <a
            href="#menu"
            onClick={e => {
              e.preventDefault();
              onMenuToggle();
            }}
            className="menuToggle"
          >
            <span>Menu</span>
          </a>
          <Drawer
            id="mainMenu"
            title="Menu"
            placement="right"
            closable={false}
            onClose={onMenuToggle}
            visible={visible}
          >
            <ul>
              <li>
                <Link activeClassName="currentPage" to="/">
                  Home
                </Link>
              </li>
              <li>
                <Link activeClassName="currentPage" to="/portfolio">
                  Portfolio
                </Link>
              </li>
              <li>
                <Link activeClassName="currentPage" to="/experience">
                  Experience
                </Link>
              </li>
            </ul>
            <a
              className="close"
              onClick={e => {
                e.preventDefault();
                onMenuToggle();
              }}
              href="#menu"
            >
              {""}
            </a>
          </Drawer>
        </li>
      </ul>
    </nav>
  );
}
